.header__lang-switch {
  color: #05c2df;
  width: 34px;
  height: 16px;
  margin-left: 3.5rem;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 130%;
  position: relative;
  transform: translateY(-3px);
}

.header__lang-switch:after {
  content: "";
  z-index: 1;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  right: 0;
}

.header__lang-switch--active:after {
  transform: translateY(-2px)rotate(360deg);
}

