.header__nav {
  align-items: flex-start;
  display: flex;
  position: relative;
}

@media only screen and (width <= 768px) {
  .header__nav {
    visibility: hidden;
    cursor: pointer;
    z-index: 20000;
    width: 38px;
  }

  .header__nav:after, .header__nav:before {
    visibility: visible;
    content: "";
    position: absolute;
  }

  .header__nav:after {
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
    width: 29px;
    height: 13px;
    transition: transform .5s, border-top .3s;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }

  .header__nav:before {
    z-index: 2;
    background-color: #fff;
    width: 20px;
    height: 2px;
    transition: transform .3s;
    top: 50%;
    right: 0;
    transform: translateX(-15.5px)translateY(-50%);
  }

  .header__nav--active {
    position: fixed;
    top: 34px;
    right: 15px;
  }
}

@media only screen and (width <= 768px) and (width <= 430px) {
  .header__nav--active {
    top: 18px;
    transform: translateY(-10px);
  }
}

@media only screen and (width <= 768px) {
  .header__nav--active:after {
    border-top: none;
    transform: translateY(-60%)rotate(405deg);
  }

  .header__nav--active:before {
    background-color: #0000;
    border-bottom: 2px solid #fff;
    width: 29px;
    padding: 7px;
    transform: translateY(-50%)rotate(-45deg)translateX(-4px)translate(0, -50%);
  }

  .header__nav--language-menu-active {
    position: fixed;
    top: 33px;
    left: 33px;
  }
}

@media only screen and (width <= 768px) and (width <= 500px) {
  .header__nav--language-menu-active {
    top: 11px;
    left: 5px;
  }
}

@media only screen and (width <= 768px) {
  .header__nav--language-menu-active:after {
    border-top: none;
    width: 14.5px;
    transform: translateY(-50%)rotate(45deg);
  }

  .header__nav--language-menu-active:before {
    width: 14.5px;
    transform: translateY(-7px)translateX(-4px)rotate(-45deg);
  }
}

